@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@400;600&display=swap);
*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

a{
  text-decoration: none;
  color: lightgray;
}

/* a:hover{
  text-decoration: none;
  color: black;
} */

body {
  font-family: 'Rubik', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #282c34;
  color: white;
  max-width: 1100px;
  margin: 0 auto;
}



.App{padding:20px;text-align:left}.won-card{background-color:#138340}.lost-card{background-color:#C43B52}

.MatchDetailCard{display:grid;grid-template-columns:1fr 1fr;padding:10px;margin-bottom:10px}.MatchDetailCard .match-date,.MatchDetailCard .match-venue,.MatchDetailCard .match-winner{margin-top:10px}.MatchDetailCard .additional-details{text-align:right}.MatchDetailCard .additional-details p{margin-bottom:20px}

.MatchSmallCard{padding:10px;margin-bottom:20px}.MatchSmallCard .match-winner{margin-top:10px}

.TeamPage{display:grid;grid-template-columns:1fr 1fr 1fr 1fr;grid-template-rows:350px 1fr 1fr;grid-column-gap:10px;grid-row-gap:10px}.TeamPage .team-header{grid-column:span 3;justify-self:start;align-self:flex-end}.TeamPage .team-header .home-page-button{padding:10px;border:1px solid white;width:100px;color:white;display:inline}.TeamPage .team-header .home-page-button .homepage-link-icon{position:relative;top:2px}.TeamPage .team-header .home-page-button:hover{box-shadow:0 8px 16px 0 rgba(0,0,0,0.2),0 6px 20px 0 rgba(0,0,0,0.19);background-color:white;color:#282c34}.TeamPage .team-header .team-title{font-size:4rem;margin-bottom:10px;margin-top:150px}.TeamPage .win-loss-section{justify-self:center;align-self:center;padding:30px;text-align:center}.TeamPage .win-loss-section p{margin-bottom:10px}.TeamPage .win-loss-section .property-title{margin-top:30px;margin-left:70px;text-align:center}.TeamPage .win-loss-section .property-title .color-box{left:45px;top:15px;width:15px;height:15px}.TeamPage .win-loss-section .property-title .win-Color{background-color:#138340}.TeamPage .win-loss-section .property-title .loss-Color{background-color:#C43B52}.TeamPage .win-loss-section .property-title p{position:relative;bottom:15px}.TeamPage .match-detail-section{grid-column:span 4}.TeamPage .match-detail-section h2{margin-bottom:5px}.TeamPage .more-link{justify-self:center;align-self:center;font-size:1.5rem}.TeamPage .more-link .link-icon{position:relative;top:5px;right:5px}

.YearSelector{padding:10px}.YearSelector li{margin-top:10px;list-style-type:none;text-align:center}.YearSelector li .year-button{padding:10px;border:none;background:none;border:1px solid white;color:white;font-size:15px}.YearSelector li .year-button:focus{padding:10px;border:none;background:none;border:1px solid white;background-color:white;color:#282c34;font-size:15px}.YearSelector li .year-button:hover{background-color:white;color:#282c34;box-shadow:0 8px 16px 0 rgba(0,0,0,0.2),0 6px 20px 0 rgba(0,0,0,0.19);cursor:pointer}

.MatchPage{display:grid;grid-template-columns:1fr 5fr;grid-column-gap:20px}.MatchPage .year-select{justify-self:end}.MatchPage .matchPage-title{margin:10px auto}.MatchPage .no-match-div{width:100%;height:150px;background-color:#1b78cf;text-align:center;font-size:30px;color:#f7f7f7;font-weight:400;padding:50px;margin-top:50px}

.TeamTile{height:150px;border:1px solid white;text-align:center}.TeamTile h2{margin-top:20px;font-size:30px}.TeamTile:hover{background-color:white;color:#282c34;cursor:pointer}

.HomePage .homepage-title{margin:20px auto;text-align:center;font-size:30px}.HomePage .team-grid{display:grid;grid-template-columns:1fr 1fr;grid-row-gap:10px;grid-column-gap:10px}

